:root {

    --idealWhite: aliceblue;
}

.mainTitle{
    margin-top: 5%;
    margin-left: 2%;

}
.bodyIntro {
    position: relative;
   
    margin-top: 5%;
    margin-left: 2%;
    max-width: 800px;
    height: 300px;
}
.mainIntro {
    margin-top: 5%;
    margin-left: 2%;
    color: var(--idealWhite);
    
}
.aboutlist {
    list-style: none;
    padding-left:0px
    
}

.colorHighlight {
    background: linear-gradient(
      to right, 
      hsl(98 100% 62%), 
      hsl(204 100% 59%)
    );
    background-clip: text;
    -webkit-text-fill-color: transparent;
}



.club-features {
    position:absolute;
    
    
    margin-top:5%;
    left: 1%;
    margin-left:30%;

}
.featureList {
    list-style: none;
    color: var(--idealWhite);
    padding-left:0px

}

@media screen and (max-width: 800px) {
    .mainTitle{
        color: var(--smallfontGreen);
        margin-top: 15%;
        margin-left: 2%;
        text-align: center;
        

    }
    .mainIntro {
        
        
        max-width :500px;
        
        margin: 0 auto;
        
    }
    .club-features {
        position: absolute;
        margin:0 auto;
        margin-top: 70%;
        max-width :500px;
    }

    

    
    

    
}
